
import React, { useState, useEffect, useMemo } from 'react';
import movieData from '../videos.json';
import blockData from '../block.json';
import '../../css/material-kit.css';
import '../../css/video.css';
import { Button, Modal, Carousel, Card, Accordion } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import $ from 'jquery';
//import Popper from 'popper.js';
import Not_found from '../../img/default_img.png'
import { disableInteractions } from '../../js/disable';
import { ToastContainer, toast } from 'react-toastify';
import 'flag-icon-css/css/flag-icons.min.css';
import Cookies from 'js-cookie';
import { format } from 'date-fns';
import { zhTW } from 'date-fns/locale';
import Marquee from "react-fast-marquee";
import { FiberManualRecord, NewReleases, ReportGmailerrorred, DownloadForOffline, FiberNew } from '@mui/icons-material';//Icon

export default function Video() {
    const [visibleMovies, setVisibleMovies] = useState(12);
    const [isLoading, setIsLoading] = useState(false);
    const [showNoMoreContent, setShowNoMoreContent] = useState(false);
    const [currentTime, setCurrentTime] = useState(new Date().getTime());
    const [modalShow, setModalShow] = useState(false);
    const [modalTitle, setModalTitle] = useState(null);
    const [modalBody, setModalBody] = useState(null);
    const [downloadStart, setdownloadStart] = useState(new Date("2024-09-01 00:00:00").getTime());
    const [downloadEnd, setdownloadEnd] = useState(new Date("2024-09-20 00:00:00").getTime());
    const [isPlaying, setIsPlaying] = useState(true);

    const futureMoviesByMonth = useMemo(() => {
        return movieData.reduce((result, movie) => {
            const movieDate = new Date(movie.openDate);
            if (movieDate > new Date()) {
                const monthYear = `${format(movieDate, 'yyMM', { locale: zhTW })}`;
                if (!result[monthYear]) {
                    result[monthYear] = [];
                }
                result[monthYear].push(movie);
            }
            return result;
        }, {});
    }, [movieData]);

    const checkForUpdate = async () => {
        try {
            const response = await fetch(`${process.env.PUBLIC_URL}/version.json`);

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            const data = await response.json();
            const cookiesVersion = Cookies.get('version');
            const daysToExpire = 365;
            const expirationDate = new Date();
            expirationDate.setTime(expirationDate.getTime() + (daysToExpire * 24 * 60 * 60 * 1000));
            if (!cookiesVersion) {
                
                Cookies.set('version', JSON.stringify({
                    value: data.version,
                    expires: expirationDate.getTime() // Store expiration time in milliseconds
                }));
                var message =
                    <div>
                        cookie
                    </div>
                toast.info(message, {
                    autoClose: 1500,
                    /* onClose: () => {
                        window.location.reload(); // Refresh the page
                    }, */
                });
                return
            } 
            const cookiesVersions = Cookies.get('version');
            const { value, expires } = JSON.parse(cookiesVersions);
            if (data.version !== value) {
                var message =
                    <div>
                        2
                    </div>
                toast.info(message, {
                    autoClose: 2000,
                    onClose: () => {
                        window.location.reload(); // Refresh the page
                        Cookies.set('version', JSON.stringify({
                            value: data.version,
                            expires: expirationDate.getTime() // Store expiration time in milliseconds
                        }));
                    },
                });
            }
        } catch (error) {
            var message =
                <div>
                    :{error}
                </div>
            toast.error(message, {
                autoClose: 1500
            });
        }
    };

    useEffect(() => {/* Load when page loaded */
        disableInteractions();
        checkForUpdate()
        const interval = setInterval(() => {
            setCurrentTime(new Date().getTime());
        }, 5000);
        return () => clearInterval(interval);
    }, []);

    useEffect(() => {/*  */
        let openedWindow;
        let is_clicked = false;
        $(document).on("click touchend", ".movie_list_item", function (event) {
            const childHref = event.target.getAttribute('data-href');
            const childTitle = event.target.getAttribute('data-movie');
            const movie = findMovieByKey("title", childTitle, movieData);
            if (movie && !is_clicked) {
                var get_cookies = Cookies.get('recent_movies')
                if (get_cookies) {
                    var new_cookies = get_cookies + ',' + movie.title;
                    Cookies.set('recent_movies', new_cookies)
                }
                is_clicked = true;
                document.querySelector('html').style.pointerEvents = 'none';
                var message =
                    <div>
                         <br />{movie.title} {event.target.text}
                    </div>
                toast.info(message, {
                    autoClose: 1500,
                    onClose: () => {
                        setModalShow(false);
                        is_clicked = false
                        document.querySelector('html').style.pointerEvents = 'all'
                        var isSafari = navigator.vendor && navigator.vendor.indexOf('Apple') > -1 &&
                            navigator.userAgent &&
                            navigator.userAgent.indexOf('CriOS') === -1 &&
                            navigator.userAgent.indexOf('FxiOS') === -1;
                        if (isSafari === true) {
                            if (openedWindow && !openedWindow.closed) {
                                openedWindow.focus();
                                openedWindow.location.href = childHref;
                            } else {
                                openedWindow = window.open(childHref, "_self");
                            }
                        } else {
                            if (openedWindow && !openedWindow.closed) {
                                openedWindow.focus();
                                openedWindow.location.href = childHref;
                            } else {
                                openedWindow = window.open(childHref, '_blank');
                            }
                        }
                    },
                });
            } else {
            }
        });
        function closeAllOpenedWindows() {
            let openedWindows = [];

            // Find all opened windows
            for (let i = 0; i < window.length; i++) {
                const win = window[i];
                if (win && win.open && !win.closed && win !== window.self) {
                    openedWindows.push(win);
                }
            }

            // Close each opened window
            openedWindows.forEach((win) => {
                win.close();
            });
        }

        var count = 0
        document.onvisibilitychange = function () {
            switch (document.visibilityState) {
                case 'hidden':
                    count = count + 1
                    setIsPlaying(false);
                    // 
                    break;
                case 'visible':
                    closeAllOpenedWindows();
                    setIsPlaying(true);
                    checkForUpdate()
                    try {
                        if (openedWindow && !openedWindow.closed) {
                            openedWindow.focus();
                            openedWindow.close();
                        }
                    } catch (e) {
                        console.error("Failed to close the window: ", e);
                    }
                    if (count >= 3) {
                        count = 0
                        //window.location.reload();
                    }
                    //window.location.reload();
                    //location.reload();
                    break;
            }
        };

    }, []);

    const handleScroll = () => {
        const scrollHeight = document.documentElement.scrollHeight;
        const scrollTop = document.documentElement.scrollTop;
        const clientHeight = document.documentElement.clientHeight;

        const movie_totla = movieData.filter((movie) => {
            const currentTime = new Date().getTime();
            const openDate = new Date(movie.openDate).getTime();
            const expiredDate = new Date(movie.expiredDate).getTime();

            return currentTime >= openDate && currentTime <= expiredDate;
        })

        if (!isLoading && scrollTop + clientHeight >= scrollHeight && visibleMovies < movie_totla.length) {
            setIsLoading(true);
            setTimeout(() => {
                setVisibleMovies(visibleMovies + 6);
                setIsLoading(false);
            }, 500); // Simulate loading time
        } else if (visibleMovies >= movie_totla.length) {
            if (window.pageYOffset + window.innerHeight >= document.body.offsetHeight) {
                setShowNoMoreContent(true);
            } else {
                setShowNoMoreContent(false);
            }
        }
    };

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, [visibleMovies, handleScroll]);

    const findMovieByKey = (key, title, movieData) => {
        return movieData.find((movie) => movie[key] === title);
    };

    const findBlockByKey = (key, title, blockData) => {
        return blockData.find((block) => {
            const start = new Date(block.startTime);
            const end = new Date(block.endTime);
            const currentDate = new Date(currentTime);

            return (
                currentDate >= start &&
                currentDate <= end
            );
        });
    };

    const findBlockNote = () => {
        return blockData.find((block) => {
            const end = new Date(block.endTime);
            const currentDate = new Date(currentTime);

            return (
                currentDate <= end
            );
        });
    };

    const blockNote = findBlockNote();

    const handleMovieBlockClick = (title) => {/* Handle the block click */
        const movie = findMovieByKey("title", title, movieData);
        if (movie) {
            if (new Date(movie.expiredDate).getTime() < currentTime) {
                var message =
                    <div>
                         {movie.title}
                    </div>
                toast.error(message, {
                    autoClose: 1500
                });
                return
            }
            if (new Date(movie.openDate).getTime() > currentTime) {
                var message =
                    <div>
                         {movie.title}
                    </div>
                toast.error(message, {
                    autoClose: 1500
                });
                return
            }
            const currentBlockData = findBlockByKey("", "", blockData);
            if (currentBlockData) {
                var message =
                    <div>
                        {currentBlockData.reason}<br /><br />
                        [{currentBlockData.endTime}]
                    </div>
                toast.info(message, {
                    autoClose: 5000
                });
                return
            }

            setModalShow(true);

            if (!movie.notAll) {
                setModalTitle(`${movie.title}  ${movie.episodes} `);
            } else {
                setModalTitle(`${movie.title}   ${movie.episodes} `);
            }
            

            // Create the episode buttons
            let episodeButtons = '<div class="d-flex flex-wrap justify-content-center">';
            for (let i = 1; i <= movie.episodes; i++) {
                const episodeLink = movie.linkTemplate.replace('?EP?', i.toString());
                episodeButtons += `
                <div class="movie_list_item"><a data-movie="${movie.title}" data-href="${episodeLink}"target="_blank"class="custom-btn btn-11"style="border: 1px dotted #3F51B5;color:white">${i}<div class="dot"></div></a></div>`;
            }
            episodeButtons += '</div>';
            setModalBody(episodeButtons);
        } else {
            var error_msg =
                <div>
                     {title} 
                </div>
            toast.error(error_msg, {
                autoClose: 1500
            });
        }
    };

    const formatOpenDate = (openDateString) => {
        const openDate = new Date(openDateString);
        return `${openDate.getFullYear()}-${String(openDate.getMonth() + 1).padStart(2, '0')}-${String(openDate.getDate()).padStart(2, '0')}`;
    };

    const [index, setIndex] = useState(0);

    const handleCarouselSelect = (selectedIndex) => {
        setIndex(selectedIndex);
    };

    const [activeIndex, setActiveIndex] = useState(null);

    const handleAccordionClick = (index) => {
        setActiveIndex(activeIndex === index ? null : index);
    };

    const handleMovieDownloadClick = (title) => {/* Handle the download block click */
        if (downloadEnd < currentTime) {
            var message =
                <div>
                    
                </div>
            toast.error(message, {
                autoClose: 1500
            });
            return
        }
        if (downloadStart > currentTime) {
            var message =
                <div>
                    
                </div>
            toast.error(message, {
                autoClose: 1500
            });
            return
        }

        setShowDownloadModal(true); // Show the modal

    };

    const [downloadProgress, setDownloadProgress] = useState({});
    const [downloadStartTime, setDownloadStartTime] = useState({});
    const [ShowDownloadModal, setShowDownloadModal] = useState(false);
    const [alreadyHavePsw, setalreadyHavePsw] = useState(false);
    const correctPassword = 'rm12024'; // Set your correct password here

    const handleDownload = async (fileName) => {
        if (downloadProgress[fileName] > 0) {
            toast.warning(`${fileName} `, {
                autoClose: 1500
            });
            return;
        }
        if (!alreadyHavePsw) {
            const userInput = prompt('');
            if (userInput === correctPassword) {
                initiateDownload(fileName); // Start download after successful authentication
                setalreadyHavePsw(true)
            } else {
                alert('');
            }
        }
        else {
            initiateDownload(fileName); // Start download after successful authentication
        }
    };

    const initiateDownload = (fileName) => {
        // Notify user that the download has started
        /* toast.info(` ${fileName}`, {
            autoClose: 5000
        }); */
        window.open(fileName, '_blank');
        
    };

    const isModalVisible = Object.keys(downloadProgress).some((fileName) => downloadProgress[fileName] > 0);

    return (
        <div>
            <ToastContainer
                progressClassName="toastProgress"
                bodyClassName="toastBody"
            />
            <Modal
                size="xl"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                show={modalShow}
                onHide={() => setModalShow(false)}
                style={{
                    display: "grid"
                }}
            >
                <div style={{ background: "#4f545c" }}>
                    <Modal.Header className="d-flex justify-content-center">
                        <Modal.Title id="contained-modal-title-vcenter" className="font-weight-bolder">
                            {modalTitle}
                        </Modal.Title>
                        <button type="button" className="btn-close text-white" aria-label="Close" onClick={() => setModalShow(false)}></button>
                    </Modal.Header>
                    <Modal.Body dangerouslySetInnerHTML={{ __html: modalBody }} style={{
                        maxHeight: 'calc(100vh - 210px)',
                        overflowY: 'auto'
                    }}>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={() => setModalShow(false)}></Button>
                    </Modal.Footer>
                </div>
            </Modal>

            <Modal show={ShowDownloadModal} size="xl" aria-labelledby="contained-modal-title-vcenter" centered onHide={() => setShowDownloadModal(false)}>
                <div style={{ background: "#4f545c" }} >
                    <Modal.Header closeButton>
                        <Modal.Title></Modal.Title>
                    </Modal.Header>
                    <Modal.Body style={{
                        maxHeight: 'calc(100vh - 210px)',
                        overflowY: 'auto'
                    }}>
                        <div style={{ marginRight: '10px', display: 'flex', alignItems: 'center', fontSize: '2rem', borderBottom: '1px solid' }}>
                            <span style={{ marginRight: '10px' }}>
                                <DownloadForOffline style={{ color: "#E8B86D", fontSize: '4rem' }} />
                            </span>
                            <a style={{ color: "white", cursor: 'pointer' }} onClick={() => handleDownload('https://drive.google.com/uc?export=download&id=1qZwD8YGuXL0DVSfAAZ8fplY9Tc7r3qJL')}>
                                []
                            </a>
                        </div>
                        <div style={{ marginRight: '10px', display: 'flex', alignItems: 'center', fontSize: '2rem', borderBottom: '1px solid' }}>
                            <span style={{ marginRight: '10px' }}>
                                <DownloadForOffline style={{ color: "#E8B86D", fontSize: '4rem' }} />
                            </span>
                            <a style={{ color: "white", cursor: 'pointer' }} onClick={() => handleDownload('https://drive.google.com/uc?export=download&id=1R-VJEoqqwqg7L-37CRZaJkmu5OzMzENS')}>
                                [2]
                            </a>
                        </div>
                        <div style={{ marginRight: '10px', display: 'flex', alignItems: 'center', fontSize: '2rem', borderBottom: '1px solid' }}>
                            <span style={{ marginRight: '10px' }}>
                                <DownloadForOffline style={{ color: "#E8B86D", fontSize: '4rem' }} />
                            </span>
                            <a style={{ color: "white", cursor: 'pointer' }} onClick={() => handleDownload('https://drive.google.com/uc?export=download&id=1pC0aIgpRFvhh_eQR-UW1kd7HclQOZhsn')}>
                                [()]
                            </a>
                        </div>
                        <div style={{ marginRight: '10px', display: 'flex', alignItems: 'center', fontSize: '2rem', borderBottom: '1px solid' }}>
                            <span style={{ marginRight: '10px' }}>
                                <DownloadForOffline style={{ color: "#E8B86D", fontSize: '4rem' }} />
                            </span>
                            <a style={{ color: "white", cursor: 'pointer' }} onClick={() => handleDownload('https://drive.google.com/uc?export=download&id=1JX9tbOa1Xs3Kv2eDmeqEi7Vk8UyWwuFR')}>
                                []
                            </a>
                        </div>
                        <div style={{ marginRight: '10px', display: 'flex', alignItems: 'center', fontSize: '2rem' }}>
                            <span style={{ marginRight: '10px' }}>
                                <DownloadForOffline style={{ color: "#E8B86D", fontSize: '4rem' }} />
                            </span>
                            <a style={{ color: "white", cursor: 'pointer' }} onClick={() => handleDownload('https://drive.google.com/uc?export=download&id=1o0-2lYAG1mc0O5Dz2b897EA2jolixCht')}>
                                []
                            </a>
                        </div>
                        {Object.keys(downloadProgress).map((fileName) => (
                            downloadProgress[fileName] > 0 && (
                                <div key={fileName} style={{ marginTop: '20px', textAlign: 'center', fontSize: '2.5rem' }}>
                                    <progress value={downloadProgress[fileName]} max="100"></progress>
                                    <div>{`${fileName}: ${Math.round(downloadProgress[fileName])}%`}</div>
                                </div>
                            )
                        ))}
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => setShowDownloadModal(false)}>
                            
                        </Button>
                    </Modal.Footer>
                </div>
            </Modal>

            <Marquee speed="100" play={isPlaying} autoFill={true} pauseOnClick={true} pauseOnHover={true} style={{ fontSize: "2.5rem", position: 'sticky', top: 0, zIndex: 1000, backgroundColor: "var(--secondary-color)" }}>

                {/* <div style={{ display: 'flex', alignItems: 'center' }}>
                    <span style={{ marginLeft: '500px', marginRight: '10px' }}>
                        <FiberNew style={{ color: "#B4E380", fontSize: '4rem' }} />
                    </span>
                    <span>
                         rm1 {formatOpenDate(downloadEnd)}
                    </span>
                </div> */}

                {blockNote ? (
                    <div style={{ display: 'flex', alignItems: 'center' }}>
                        <span style={{ marginLeft: '50px', marginRight: '10px' }}>
                            <ReportGmailerrorred style={{ color: "red", fontSize: '4rem' }} />
                        </span>
                        <span>
                            {blockNote.reason}
                            [{blockNote.startTime}]  [{blockNote.endTime}]
                        </span>
                    </div>
                ) : (
                    <span style={{ marginLeft: '200px' }}>No upcoming service interruptions.</span>
                )}

                {Object.keys(futureMoviesByMonth)
                    .sort()
                    .map((monthYear, index) => (
                        <div key={index} style={{ display: 'flex', alignItems: 'center' }}>
                            <span style={{ marginLeft: '200px' }}>
                                <NewReleases style={{ color: "lightgreen", fontSize: '4rem' }} />
                            </span>
                             {monthYear}:

                            {futureMoviesByMonth[monthYear].map((movie, movieIndex) => (
                                <span style={{ listStyle: 'none', textDecoration: "underline" }} key={movieIndex}>
                                    <span style={{ marginLeft: '50px' }}>
                                        <FiberManualRecord style={{ width: '20px', height: '20px' }} />
                                    </span>
                                    {movie.title} ({movie.episodes}) - {movie.language}
                                    <span style={{ marginLeft: '50px' }}>
                                    </span>
                                </span>
                            ))}
                        </div>
                    ))}
            </Marquee>

            <h1 className="m-2">:</h1>

            <main >
                {/* <div className="movie" onClick={() => handleMovieDownloadClick()} key={index}>
                    <div className="img_container">
                        <img src={Not_found} alt="" className="movie_img set_size" loading="lazy"
                            onError={(e) => {
                                e.currentTarget.onerror = null;
                                e.currentTarget.src = { Not_found };
                            }}
                        />

                    </div>
                    <div className="movie-info">
                        <h3></h3>
                    </div>
                    <div className="release_date" style={{ fontSize: "1.5rem" }}>

                        {(() => {

                            if (downloadStart - currentTime > 0) {
                                return (
                                    <span>:{formatOpenDate(downloadStart)}</span>
                                );
                            }
                        })()}

                    </div>
                    <div className="release_date" style={{ fontSize: "1.5rem" }}>
                        : <span>
                            {(() => {
                                const remainingTime = downloadEnd - currentTime;
                                const openDate = downloadStart

                                if (remainingTime > 0 && openDate - currentTime < 0) {
                                    const days = Math.floor(remainingTime / (1000 * 60 * 60 * 24));
                                    const hours = Math.floor((remainingTime % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
                                    const minutes = Math.floor((remainingTime % (1000 * 60 * 60)) / (1000 * 60));
                                    const seconds = Math.floor((remainingTime % (1000 * 60)) / 1000);
                                    return (
                                        <span className='bg-gradient'>
                                            {days} {hours} {minutes} {seconds}
                                        </span>
                                    );
                                } else if (openDate - currentTime > 0) {
                                    return "";
                                } else {
                                    return "";
                                }
                            })()}
                        </span>
                    </div>
                </div> */}
                {movieData.filter((movie) => {
                    const openDate = new Date(movie.openDate).getTime();
                    const expiredDate = new Date(movie.expiredDate).getTime();
                    const threeDaysInMs = 3 * 24 * 60 * 60 * 1000; // 3 days in milliseconds

                    return (
                        (currentTime >= openDate && currentTime <= expiredDate) ||
                        (currentTime + threeDaysInMs >= openDate && currentTime <= openDate)
                    );
                }).slice(0, visibleMovies).map((movie, index) => (
                    <div className="movie" onClick={() => handleMovieBlockClick(movie.title)} key={index}>
                        <div className="img_container">
                            <img
                                src={movie.image || Not_found}
                                alt=""
                                className="movie_img set_size"
                                loading="lazy"
                                onError={(e) => {
                                    e.currentTarget.onerror = null;
                                    e.currentTarget.src = { Not_found };
                                }}
                            />
                            {(() => {
                                const createDate = new Date(movie.createDate).getTime();
                                const editDate = new Date(movie.editDate).getTime();
                                const openDate = new Date(movie.openDate).getTime();

                                if (openDate - currentTime > 0) {
                                    return (
                                        <span className="new_release" style={{ letterSpacing: 0 }}>
                                            <i className="new_release_icon fa-solid fa-bullhorn"></i>
                                        </span>
                                    );
                                }
                                if (86400000 * 3 > currentTime - editDate) {
                                    return (
                                        <span className="new_release" style={{ letterSpacing: 0 }}>
                                            <i className="new_release_icon fa-solid fa-bullhorn"></i>
                                        </span>
                                    );
                                }
                                if (86400000 * 3 > currentTime - createDate) {
                                    return (
                                        <div className="new-sticker">
                                            <span className="sticker"></span>
                                            <span className="new">!</span>
                                            <i className='new_release_icon fa-solid fa-bullhorn'></i>
                                        </div>
                                    )
                                }
                            })()}
                        </div>
                        <div className="movie-info">
                            <h3>{movie.title}</h3>
                            {(() => {
                                if (!movie.notAll) {
                                    return (
                                        <span >{movie.episodes} </span>
                                    );
                                } else {
                                    return (
                                        <span > {movie.episodes} </span>
                                    );
                                }
                            })()}
                           
                        </div>
                        <div className="release_date" style={{ fontSize: "1.5rem" }}>

                            {(() => {
                                const openDate = new Date(movie.openDate).getTime();

                                if (openDate - currentTime > 0) {
                                    return (
                                        <span>:{formatOpenDate(movie.openDate)}</span>
                                    );
                                }
                            })()}

                        </div>
                        <div className="release_date" style={{ fontSize: "1.5rem" }}>

                            : <span>
                                {(() => {
                                    const expiredDate = new Date(movie.expiredDate).getTime();
                                    const remainingTime = expiredDate - currentTime;
                                    const openDate = new Date(movie.openDate).getTime();

                                    if (remainingTime > 0 && openDate - currentTime < 0) {
                                        const days = Math.floor(remainingTime / (1000 * 60 * 60 * 24));
                                        const hours = Math.floor((remainingTime % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
                                        const minutes = Math.floor((remainingTime % (1000 * 60 * 60)) / (1000 * 60));
                                        const seconds = Math.floor((remainingTime % (1000 * 60)) / 1000);
                                        return (
                                            <span className='bg-gradient'>
                                                {days} {hours} {minutes} {seconds}
                                            </span>
                                        );
                                    } else if (openDate - currentTime > 0) {
                                        return "";
                                    } else {
                                        return "";
                                    }
                                })()}
                            </span>
                        </div>
                        {/* <div className="overview">
                                <h3>Overview</h3>
                                <p className='normal'>{movie.overview}</p>
                            </div> */}
                    </div>
                ))}
            </main>
            {isLoading &&
                <div className="loading">
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                    <div className="loading__letter">.</div>
                    <div className="loading__letter">.</div>
                    <div className="loading__letter">.</div>
                </div>}

            {showNoMoreContent &&
                <div className="loading">
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                    <div className="loading__letter"></div>
                </div>}
            <div>
            </div>
        </div>
    );
}